<div class="topic-tree"><div class="title">Topics<div class="options"><icon-expand-down-one class="icon expand-all" size="16px" title="expand-all" @click="expandAll()"></icon-expand-down-one><icon-fold-up-one class="icon fold-all" size="16px" title="fold-all" @click="foldAll()"></icon-fold-up-one></div></div><div class="content"><el-tree class="tree" ref="ref_tree" :data="topic_tree_data" node-key="code_full" @node-click="onNodeClick" :current-node-key="current_topic"><template #default="{ node, data }"><span class="title" :class="current_topic === data.code_full ? 'o-current' : ''">{{   data.label   }}</span></template></el-tree></div></div>