<div class="topic" data-topic="6"><h1>Application: A dragscrollable sticky table</h1><p>Drag the table head to scroll table horizontally.<br/>Drag the first column to scroll table vertically.<br/>The sticky ability is carried by <router-link to="/demos/stick-element/">stick-element</router-link>.</p><demo-box><template #=""><div class="container" ref="ref_container"><table><tr><td><table class="col-1-drag" ref="ref_col1_drag"><thead ref="ref_col1_head"><tr><th><div>row_n</div></th></tr></thead><tbody><tr v-for="(row, row_n) in dataset.rows" :key="row_n"><td>{{   row_n + 1   }}</td></tr></tbody></table></td><td><table class="main"><thead ref="ref_thead"><tr><th v-for="(column_name) in dataset.columns" :key="column_name"><div>{{   column_name   }}</div></th></tr></thead><tbody><tr v-for="(row, row_n) in dataset.rows" :key="row_n"><td v-for="(item, item_n) in row" :key="item_n">{{   item   }}</td></tr></tbody></table></td><td class="shadow-margin-right"></td></tr></table></div></template></demo-box></div>