<div class="topic" data-topic="2"><h1>Cross-level sticky</h1><p>Element can be sticky to any container, rather than only its direct parent.<br/>For the example below, cells are apparently not the direct children of the container, still they can be sticky.</p><demo-box><template #=""><div class="container" ref="ref_container"><table><thead><tr><th class="th1" ref="ref_th1"><span>th1</span></th><th class="th2"><span>th2</span></th><th class="th3"><span>th2</span></th><th class="th4"><span>th2</span></th><th class="th5"><span>th2</span></th></tr></thead><tbody><tr><td>11</td><td>12</td><td>13</td><td>14</td><td>15</td></tr><tr><td>21</td><td class="highlighted" ref="ref_td22">22</td><td>23</td><td>24</td><td>25</td></tr><tr><td>31</td><td>32</td><td>33</td><td>34</td><td>35</td></tr><tr><td>41</td><td>42</td><td>43</td><td>44</td><td>45</td></tr><tr><td>51</td><td>52</td><td>53</td><td>54</td><td>55</td></tr></tbody></table></div></template></demo-box></div>