<div class="topic" data-topic="3-5"><h1>Sensitivity</h1><p>To accurately define dragscroll's sensitivity, scale the relation of drag_trigger_threshold and scroll_respond_vector.</p><h3>Normal</h3><p>For the example below, 1px drag triggers 1px scroll.</p><demo-box><template #=""><div class="container" ref="ref_scrollable"><img src="@/assets/images/landscape-coast.jpg" alt="landscape-coast" draggable="false" ref="ref_draggable"/></div><div class="ruler-container" ref="ref_ruler_scrollable"><div class="ruler-element" ref="ref_ruler_draggable"><span v-for="n in 20" :key="n">{{   n   }}</span></div></div></template><template #code=""><highlighted-code code_language="html">{{   CodeSnippets['subtopic_1'].template   }}</highlighted-code><highlighted-code code_language="typescript">{{   CodeSnippets['subtopic_1'].script   }}</highlighted-code></template></demo-box><h3>Less sensitive</h3><p>For the example below, 5px drag triggers 1px scroll.</p><demo-box><template #=""><div class="container" ref="ref_scrollable_1"><img src="@/assets/images/landscape-coast.jpg" alt="landscape-coast" draggable="false" ref="ref_draggable_1"/></div><div class="ruler-container" ref="ref_ruler_scrollable_1"><div class="ruler-element" ref="ref_ruler_draggable_1"><span v-for="n in 20" :key="n">{{   n   }}</span></div></div></template><template #code=""><highlighted-code code_language="html">{{   CodeSnippets['subtopic_2'].template   }}</highlighted-code><highlighted-code code_language="typescript">{{   CodeSnippets['subtopic_2'].script   }}</highlighted-code></template></demo-box><h3>More sensitive</h3><p>For the example below, 1px drag triggers 5px scroll.</p><demo-box><template #=""><div class="container" ref="ref_scrollable_2"><img src="@/assets/images/landscape-coast.jpg" alt="landscape-coast" draggable="false" ref="ref_draggable_2"/></div><div class="ruler-container" ref="ref_ruler_scrollable_2"><div class="ruler-element" ref="ref_ruler_draggable_2"><span v-for="n in 20" :key="n">{{   n   }}</span></div></div></template><template #code=""><highlighted-code code_language="html">{{   CodeSnippets['subtopic_3'].template   }}</highlighted-code><highlighted-code code_language="typescript">{{   CodeSnippets['subtopic_3'].script   }}</highlighted-code></template></demo-box><h3>Scaled sensitive</h3><p>For the example below, 10px drag triggers 10px scroll.</p><demo-box><template #=""><div class="container" ref="ref_scrollable_3"><img src="@/assets/images/landscape-coast.jpg" alt="landscape-coast" draggable="false" ref="ref_draggable_3"/></div><div class="ruler-container" ref="ref_ruler_scrollable_3"><div class="ruler-element" ref="ref_ruler_draggable_3"><span v-for="n in 20" :key="n">{{   n   }}</span></div></div></template><template #code=""><highlighted-code code_language="html">{{   CodeSnippets['subtopic_4'].template   }}</highlighted-code><highlighted-code code_language="typescript">{{   CodeSnippets['subtopic_4'].script   }}</highlighted-code></template></demo-box></div>