
    import { defineComponent } from "vue"

    export default defineComponent({
        name: 'links',
        components:
        {
            
        },
        props:
        {
            readme:
            {
                type: String as () => string|null,
                default: null,
            },
            github:
            {
                type: String as () => string|null,
                default: null,
            },
            npm:
            {
                type: String as () => string|null,
                default: null,
            },
        },
        setup()
        {
            // data ----------------------------------------------------------------------------------------------------
            // 0 //
            // methods -------------------------------------------------------------------------------------------------
            function openNewTab(url: string)
            {
                window.open(url, '_blank')
            }

            // return --------------------------------------------------------------------------------------------------
            return {
                openNewTab,
            }
        },
    })
