<div id="tsp-graphing-app"><div class="header"><div class="title">Solution Visualization of Travel Salesman Problem</div><div class="helpers"><icon-helpcenter class="entry introduction" title="introduction" size="18" @click="setDisplayIntroduction(true)"></icon-helpcenter><icon-github-one class="entry github" title="github" size="18" @click="toGithub()"></icon-github-one><icon-table-report class="entry report" title="experiment report" size="18" @click="toReport()"></icon-table-report></div></div><div class="body"><el-tabs class="big-tabs" type="border-card"><el-tab-pane class="pane experiment" label="Experiment" :style="{ width: if_display_dashboard ? 'calc(100% - 400px)' : 'initial'}"><section class="time-cost"><div class="title">Time Cost</div><div class="description">Total time cost of running each trial.</div><div class="figure time-cost" ref="ref_figure_time_cost"></div></section><section class="result"><div class="title">Result</div><div class="description">Result of each generation's maximum, minimum, average distance.</div><div class="figure result" ref="ref_figure_result"></div></section></el-tab-pane><el-tab-pane class="pane evolution" label="Evolution" :style="{ width: if_display_dashboard ? 'calc(100% - 400px)' : 'initial'}"><section class="xxxx"><div class="title">Evolution</div><div class="description">Trend of each generation's maximum, minimum, average distance.</div><div class="figure evolution" ref="ref_figure_evolution"></div></section></el-tab-pane><el-tab-pane class="pane generation" label="Generation" :style="{ width: if_display_dashboard ? 'calc(100% - 400px)' : 'initial'}"><section class="xxxx"><div class="title">Generation</div><div class="description">Eventual arrivals of a generation.</div><div class="figure generation" ref="ref_figure_generation"></div></section></el-tab-pane><el-tab-pane class="pane individual" label="Individual" :style="{ width: if_display_dashboard ? 'calc(100% - 400px)' : 'initial'}"><section class="xxxx"><div class="title">Individual</div><div class="description">The individual solution (result travel route).</div><div class="figure individual" ref="ref_figure_individual"></div></section></el-tab-pane><el-tab-pane class="pane plain-map" label="Plain Map" :style="{ width: if_display_dashboard ? 'calc(100% - 400px)' : 'initial'}"><section class="xxxx"><div class="title">Plain map</div><div class="description">Plain city map</div><div class="figure plain-map" ref="ref_figure_plain_map"></div></section></el-tab-pane></el-tabs><el-select class="selector case" v-model="opting_case" placeholder="select a case" title="map case"><el-option :key="0" :value="'WesternSahara'"></el-option><el-option :key="1" :value="'Uruguay'" disabled="disabled"></el-option><el-option :key="2" :value="'Canada'" disabled="disabled"></el-option></el-select><el-select class="selector algorithm" v-model="opting_algorithm" placeholder="select an algorithm" title="algorithm level"><el-option :key="0" :value="'basic'" disabled="disabled"></el-option><el-option :key="1" :value="'advanced'"></el-option></el-select><el-button class="dashboard button" size="medium" @click="toggleDashboard()" type="primary" plain="plain">Dashboard</el-button><div class="dashboard panel" v-show="if_display_dashboard"><div class="controls"><section class="trial"><div class="title">Trial</div><el-slider class="slider" v-model="trial_n" :min="1" :max="trial_all" show-input="show-input" @change="drawEvolution();drawGeneration();drawIndividual();"></el-slider></section><section class="generation"><div class="title">Generation</div><el-slider class="slider" v-model="generation_n" :min="1" :max="generation_all" show-input="show-input" @change="drawGeneration();drawIndividual();"></el-slider></section><section class="individual"><div class="title">Individual</div><el-slider class="slider" v-model="individual_n" :min="1" :max="2" show-input="show-input" @change="drawIndividual();"></el-slider></section></div><div class="details"><div class="item generation">Generation<div class="item average-distance">Average Distance:<span>{{   current_generation_avg_distance   }}</span></div><div class="item shortest-distance">Shortest Distance:<span>{{   current_generation_best_distance   }}</span></div><div class="item longtest-distance">Longtest Distance:<span>{{   current_generation_worst_distance   }}</span></div></div><div class="item individual">Individual<div class="item progress">Progress<span>{{   '---'   }}</span></div></div><div class="item travel-route">Travel Route<div class="route-sequence">{{   travel_route   }}</div></div></div></div></div><el-dialog title="Introduction" v-model="if_display_introduction" :style="{ 'max-height': '640px', 'overflow': 'auto' }" custom-class="modal introduction"><div class="markdown" ref="ref_markdown_mount_node"><markdown-box :markdown_string="MarkdownIntroduction"></markdown-box></div></el-dialog></div>