<div class="topic" data-topic="5"><h1>Avoid handles</h1><p>Dragscroll won't take effect on those elements assigned as avoids.<br/>For the example below, order number divs are avoided</p><demo-box><template #=""><div class="ruler-container" ref="ref_ruler_scrollable"><div class="ruler-element" ref="ref_ruler_draggable"><span v-for="(value, index) of order_numbers" :key="index"><span @click="toggleNumber(index - 1)" :class="order_numbers[index - 1] ? 'o-active' : ''">{{   index   }}</span></span></div></div></template><template #code=""><highlighted-code code_language="html">{{   CodeSnippets['subtopic_1'].template   }}</highlighted-code><highlighted-code code_language="typescript">{{   CodeSnippets['subtopic_1'].script   }}</highlighted-code></template></demo-box></div>