
    import { defineComponent, nextTick, onMounted, ref } from "vue"

    import DemoBox from "@/core/interface/components/_demo-box/index.vue"

    import stickElement from "@/plugins/stick-element"

    export default defineComponent({
        name: 'topic-2',
        components:
        {
            [DemoBox.name]: DemoBox,
        },
        setup()
        {
            // data ----------------------------------------------------------------------------------------------------
            // 0 //

            // dom refs //
            const ref_container = ref() as { value: HTMLElement }
            const ref_th1 = ref() as { value: HTMLElement }
            const ref_td22 = ref() as { value: HTMLElement }

            // methods -------------------------------------------------------------------------------------------------

            // lifecycles ----------------------------------------------------------------------------------------------
            onMounted(() => {
                nextTick(() => {
                    stickElement(ref_container.value, ref_th1.value, { top: 50 })
                    stickElement(ref_container.value, ref_td22.value, { top: 200, left: 200 })
                })
            })

            // return --------------------------------------------------------------------------------------------------
            return {
                ref_container,
                ref_th1,
                ref_td22,
            }
        },
    })
